import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhEinvoice, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhType } from 'src/app/sales/interface/vh-type';
import { BillService } from 'src/app/sales/services/bill.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { PrintService } from 'src/app/sales/services/print.service';
@Component({
  selector: 'app-e-invoice-tiki',
  templateUrl: './e-invoice-tiki.component.html',
  styleUrls: ['./e-invoice-tiki.component.scss']
})
export class EInvoiceTikiComponent implements OnInit {
  searchValue: any;
  searchProductAppValue: any = '';
  salesOrder: Array<any> = [];
  salesSubOrder: Array<any> = [];
  selectedIndex: number = 0;
  loadingPickup: boolean = false;
  loadingConfirm: boolean = false;
  isVisibleConfirm: boolean = false;
  billSelected: any = {};
  list_status_bill: Array<any> = [
    { name: "All", status: "all" },
    { name: "Wait for confirmation", status: "queueing", main_state: 'awaiting_confirmation' },//1
    { name: "Đang đóng gói", status: "queueing", main_state: 'processing' },//2
    { name: "Lấy hàng", main_state: 'processing', main_substate: "picked_up_from_seller", main_substate1: "picking_up_from_seller" },//3
    { name: "Lấy hàng thất bại", main_substate1: "pickup_failed_2", main_substate: 'pickup_failed_1' },//4
    { name: "Tình trạng giao hàng", status: "ready_to_ship", status2: "shipping", main_state: 'shipping' },//5
    { name: "Giao hàng thành công", status: "successful_delivery" },//6
    { name: "Giao hàng thất bại", status: "handover_to_partner", main_state: 'shipping', main_substate: "3pl_delivery_failed_1", main_substate2: "3pl_delivery_failed_2", main_substate3: "3pl_delivery_failed_3" },
    { name: "Đang trả hàng về cho nhà bán", status: "handover_to_partner", main_substate: "3pl_returning_to_seller", main_state: 'shipping' },//8
    { name: "Đã hủy (Chưa giao hàng)", status: "canceled", main_state: 'canceled', confirmation_status: 'waiting', confirmation_status1: 'seller_confirmed' },
    { name: "Trả hàng thành công", status: "canceled", main_state: 'canceled', confirmation_status: 'ready_to_pick' },
  ]
  heightScroll: any;
  widthScroll: any;
  public date: Date[] = [new Date(), new Date()];
  startTimeBackUp: Date;
  endTimeBackUp: Date;
  load_status: boolean = false;
  dataRestore: any;
  /**Giới hạn thời gian cho xem bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true;
    return false;
  };
  listAddress: Array<any> = [];
  listTimes: Array<any> = [];
  infoShop: any = {};
  listProductOfBranch: Array<any> = []; // sp của phần mềm có liên kết với tiki
  listProductApp: Array<any> = []; //sp của phần mềm
  listSubProductApp: Array<any> = []; //sp của app chứa tiki[]
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id;
  arrayHidden: Array<any> = []; //mảng này chứa id của những sp đang tạm ẩn

  currentBranch = this.vhQuerySales.getlocalBranch(this.vhQuerySales.getDefaultBranch()._id);
  infoBranch = this.vhQuerySales.getlocalBranch(this.vhQuerySales.getDefaultBranch()._id);



  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private datePipe: DatePipe,
    private nzModalService: NzModalService,
    private vhAuth: VhAuth,
    private vhQuery: VhQuery,
    private vhEinvoice: VhEinvoice,
    private billService: BillService,
    public printService: PrintService,
  ) {
    this.dataRestore = this.router.getCurrentNavigation().extras.state?.dataRestore;
  }



  ngOnInit(): void {
    this.disabledDate(new Date());
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue;
      this.salesOrder = this.dataRestore.salesOrder;
      this.salesSubOrder = this.dataRestore.salesSubOrder;
      this.countOrderByStatus(this.salesOrder);
      this.load_status = true;
      this.selectedIndex = this.dataRestore.selectedIndex;
      this.date = this.dataRestore.date;
    }
    this.infoShop = this.vhQuerySales.getlocalShopListTiki()[0];

    this.listProductOfBranch = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden).filter(item => item[this.id_branch] && item[this.id_branch].tikis && item[this.id_branch].tikis.length >= 0);

    let localProducts: Array<any> = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden);
    this.listProductApp = localProducts.map(item => {
      if (item[this.id_branch] && item[this.id_branch].tikis && item[this.id_branch].tikis.length) { //filter những sp tiki thuộc id tiki đã chọn
        item[this.id_branch].tikis = [...item[this.id_branch].tikis.filter(prod => prod.id_shop == this.infoShop._id)];
      }
      return { ...item, ...this.vhQuerySales.getUnit_byRatio(item.units, 1) };
    });

    this.listSubProductApp = [...this.listProductApp];
  }



  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 1209600000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("Thời gian xem không quá 15 ngày"), "alert-toast");
      this.date = [event[0], new Date(event[0].getTime() + 1209600000)];
    }
  }



  search(value: string) {
    this.segmentChanged(this.selectedIndex);
    /** Mảng bill cần search */
    let valArray: string[] = value.split(',').map(val => this.vhAlgorithm.changeAlias(val?.toString().trim().toLowerCase())).filter(val => val?.length > 0);
    if (valArray?.length > 0) {
      // search theo code
      this.salesOrder = this.salesOrder.filter(item => {
        return valArray.some(val => {
          return this.vhAlgorithm.changeAlias(item?.code?.toString())?.includes(val);
        });
      });
    }
  }



  /**
   * search tên sản phẩm trong app viethas
   * @example this.search('Sản phẩm A')
   */
  searchProductApp(value: string) {
    let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase());
    this.listProductApp = this.vhAlgorithm.searchList(val, [...this.listSubProductApp],  ['name','barcode','item_code']);
  }



  segmentChanged(event) { // sau này sẽ filter theo trạng thái qua hàm này 
    if (event == 0) this.salesOrder = this.salesSubOrder
    else {
      switch (event) {
        case 1: 
        case 2: 
        case 5: {
          this.salesOrder = this.salesSubOrder.filter(item => (item.status == this.list_status_bill[event].status || item.status == this.list_status_bill[event].status2) && item.main_state == this.list_status_bill[event].main_state)
          break;
        };
        case 3: {
          this.salesOrder = this.salesSubOrder.filter(item => item.main_state == this.list_status_bill[event].main_state && (item.main_substate == this.list_status_bill[event].main_substate || item.main_substate == this.list_status_bill[event].main_substate1))
          break;
        };

        case 4: {
          this.salesOrder = this.salesSubOrder.filter(item => (item.main_substate == this.list_status_bill[event].main_substate1 || item.main_substate == this.list_status_bill[event].main_substate))
          break;
        }
        case 6: {
          this.salesOrder = this.salesSubOrder.filter(item => item.status == this.list_status_bill[event].status)
          break;
        };
        case 8: 
        case 7: {
          this.salesOrder = this.salesSubOrder.filter(item => item.status == this.list_status_bill[event].status && item.main_state == this.list_status_bill[event].main_state && (item.main_substate == this.list_status_bill[event].main_substate || item.main_substate == this.list_status_bill[event].main_substate2 || item.main_substate == this.list_status_bill[event].main_substate3))
          break;
        }
        case 9: 
        case 10: {
          this.salesOrder = this.salesSubOrder.filter(item => item.status == this.list_status_bill[event].status && item.main_state == this.list_status_bill[event].main_state && (item.items[0].confirmation.status == this.list_status_bill[event].confirmation_status || item.items[0].confirmation.status == this.list_status_bill[event].confirmation_status1))
          break;
        }
      }
    }
  }
  


  /** Map chứa số lượng đơn hàng theo trạng thái */
  orderCountByStatus: any = {
    'All': 0,
    'Wait for confirmation': 0,
    'Đang đóng gói': 0,
    'Lấy hàng': 0,
    'Lấy hàng thất bại': 0,
    'Tình trạng giao hàng': 0,
    'Giao hàng thành công': 0,
    'Giao hàng thất bại': 0,
    'Đang trả hàng về cho nhà bán': 0,
    'Đã hủy (Chưa giao hàng)': 0,
    'Trả hàng thành công': 0,
  }

  /**
   * Đếm số lượng đơn hàng theo trạng thái
   * @param orders
   */
  countOrderByStatus(orders) {
    this.orderCountByStatus['All'] = orders.length;
    this.orderCountByStatus['Wait for confirmation'] = orders.filter(order => order.status === 'queueing' && order.main_state === 'awaiting_confirmation').length;
    this.orderCountByStatus['Đang đóng gói'] = orders.filter(order => order.status === 'queueing' && order.main_state === 'processing').length;
    this.orderCountByStatus['Lấy hàng'] = orders.filter(order => order.main_state === 'processing' && (order.main_substate === 'picked_up_from_seller' || order.main_substate === 'picking_up_from_seller')).length;
    this.orderCountByStatus['Lấy hàng thất bại'] = orders.filter(order => order.main_substate === 'pickup_failed_1' || order.main_substate === 'pickup_failed_2').length;
    this.orderCountByStatus['Tình trạng giao hàng'] = orders.filter(order => (order.status === 'ready_to_ship' || order.status === 'shipping') && order.main_state === 'shipping').length;
    this.orderCountByStatus['Giao hàng thành công'] = orders.filter(order => order.status === 'successful_delivery').length;
    this.orderCountByStatus['Giao hàng thất bại'] = orders.filter(order => order.status === 'handover_to_partner' && order.main_state === 'shipping' && (order.main_substate === '3pl_delivery_failed_1' || order.main_substate === '3pl_delivery_failed_2' || order.main_substate === '3pl_delivery_failed_3')).length;
    this.orderCountByStatus['Đang trả hàng về cho nhà bán'] = orders.filter(order => order.status === 'handover_to_partner' && order.main_substate === '3pl_returning_to_seller' && order.main_state === 'shipping').length;
    this.orderCountByStatus['Đã hủy (Chưa giao hàng)'] = orders.filter(order => order.status === 'canceled' && order.main_state === 'canceled' && (order.items[0].confirmation.status === 'waiting' || order.items[0].confirmation.status === 'seller_confirmed')).length;
    this.orderCountByStatus['Trả hàng thành công'] = orders.filter(order => order.status === 'canceled' && order.main_state === 'canceled' && order.items[0].confirmation.status === 'ready_to_pick').length;
  }



  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki'], {
      state: {
        dataRestore: { dataRestore: this.dataRestore }
      }
    });
  }



  showReport() {
    this.load_status = true;
    this.salesOrder = [];
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.getSalesOrderListTiki(new Date(this.datePipe.transform(new Date(this.date[0].setHours(0, 0, 0)), "yyyy-MM-dd HH:mm:ss").toString()), new Date(this.datePipe.transform(new Date(this.date[1].setHours(23, 59, 59)), "yyyy-MM-dd HH:mm:ss").toString()), this.infoShop.access_token.toString(), this.infoShop._id.toString())
        .then((result: any) => {
          this.vhComponent.hideLoading(0);
          this.salesOrder = result.map(item => { return { ...item, create_at: new Date(item.create_time * 1000), checked: true }; });
          this.salesSubOrder = this.salesOrder;
          this.countOrderByStatus(this.salesOrder);
          this.segmentChanged(this.selectedIndex);
        })
        .catch(err => this.vhComponent.hideLoading(0));
    });
  }



  calculateTotal(bill) {
    return +bill['price'] - bill['voucher'] + bill['shipping_fee'];
  }



  ngAfterViewChecked() {
    if (document.querySelector("#order-status-tiki") && document.querySelector(".ant-tabs-tab") && document.querySelector(".ant-table-thead") && document.querySelector(".order-status-tiki-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#order-status-tiki").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".order-status-tiki-header").clientHeight - document.querySelector(".ant-tabs-tab").clientHeight - 90 + "px";
    }
    if (document.getElementById("order-status-tiki"))
      this.widthScroll = document.getElementById("order-status-tiki").clientWidth + "px";
    this.cdRef.detectChanges();
  }



  gotoDetail(bill) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiki/order-status/detail'], { state: { bill: bill, dataRestore: { salesOrder: this.salesOrder, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesSubOrder: this.salesSubOrder, selectedIndex: this.selectedIndex } } });
  }



  allChecked = true;
  indeterminate = true;



  /**
   * Check tất cả
   * @param value
   */
  checkAll(value: boolean): void {
    this.salesOrder.forEach(data => {
      if (!data.disabled) {
        data.checked = value;
      }
    });
    this.refreshStatus();
  }

  /** Có hiển thị modal chỉnh sửa customer tax hay không */
  isShowModalEditCustomerTax: boolean = false;
  tempCustomerTax: any = null;
  /**
   * Khi nhấn edit cột thông tin khách hàng
   */
  editTax(invoice) {
    this.tempCustomerTax = invoice;
    this.isShowModalEditCustomerTax = true;
  }



  /**
   * Khi ok trong modal chỉnh sửa customer tax
   * @param customerTaxInfo
   */
  onOkEditTax(customerTaxInfo) {
    this.isShowModalEditCustomerTax = false;

    Object.assign(this.tempCustomerTax, customerTaxInfo);
  }

  findProductLinked(item) {
    for (let productOfBranch of this.listProductOfBranch) {
      let findProduct = productOfBranch[this.id_branch].tikis.find(data => data.product_id == item.product.id);
      if (findProduct) {
        return productOfBranch;
      }
    }

    return null;
  }

  /**
   * Kiểm tra sản phẩm đã được lien kết hay chưa
   *
   */
  isLinkedToProduct(item): boolean {
    for (let productOfBranch of this.listProductOfBranch) {
      let findProduct = productOfBranch[this.id_branch].tikis.find(data => data.product_id == item.product.id);
      if (findProduct) {
        return true;
      }

    };
    return false;
  }

  visible_affiliate: boolean = false; // hiển thị modal chọn sản phẩm phần mềm liên kết
  productTikiSelected: any = null; // sản phẩm tiki được chọn để lien kết


  openModelSelectProductsTiki(productTiki) {
    this.productTikiSelected = productTiki;
    this.visible_affiliate = true;
  }


  /**
   * Tắt modal và clone mảng product ra một mảng mới để tránh nhớ lại product cũ bị checked
   */
  cancelAffiliate() {
    this.visible_affiliate = false;
  }


  /**
    * Liên kết sản phẩm từ tiki với sản phẩm tại cửa hàng.
    * @param product Sản phẩm của cửa hàng
    */
  acceptAfiliate(product) {
    const isZeroPriceCombo = product.type === VhType.COMBO && product.units[0].price === 0;
    const confirmZeroPriceComboMessage = this.lang.translate("This product is a combo with a price of 0. Do you still want to link it with the product", { platform: 'Tiki' }) + "?";

    this.nzModalService.confirm({
      nzTitle: '<i>' + (isZeroPriceCombo ? confirmZeroPriceComboMessage : this.lang.translate("Confirm")) + '</i>',
      nzIconType: "info-circle",
      nzCancelText: this.lang.translate("Cancel"),
      nzOnOk: () => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          const id_product_selected = product.id_subproduct ? product.id_subproduct : product['_id'];
          this.vhQuerySales.addMergeTikiProduct(id_product_selected, [this.productTikiSelected.product.id], this.infoShop['_id']).then((bool) => {
            this.vhComponent.hideLoading().then(() => {
              setTimeout(() => {
                this.visible_affiliate = false;
                this.listProductOfBranch = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden).filter(item => item[this.id_branch] && item[this.id_branch].tikis && item[this.id_branch].tikis.length >= 0);
                this.vhComponent.alertMessageDesktop('success', this.lang.translate("Product linked successfully"));
              }, 300);
            });
          }).catch(err => {
            console.error(err);
            this.visible_affiliate = false;
            this.vhComponent.alertMessageDesktop('error', this.lang.translate("The product link failed"));
            this.vhComponent.hideLoading(0);
          });
        });
      }
    });
  }

  /**Xóa những sp đã được liên kết rồi ra khỏi mảng của modal danh sách sp liên kết
   * @param listProduct : danh sách sp đã lk với sàn
   * @param listProductApp : toàn bộ danh sách sp của app
   * @example let daa =  this.clearProductShow([], [])
   *
  */
  clearProductShow(listProduct, listProductApp) {
    for (let prodApp of listProductApp) {
      if (prodApp[this.id_branch] && prodApp[this.id_branch].tikis && prodApp[this.id_branch].tikis.length) {
        for (let item of prodApp[this.id_branch].tikis) {
          let indexProd = listProduct.findIndex(prod => prod.product_id == item.product_id);
          if (indexProd >= 0)
            listProduct.splice(indexProd, 1);
        }
      }
    }
    return listProduct;
  }



  /**
   * Check 1 cái
   */
  refreshStatus(): void {
    const validData = this.salesOrder.filter(value => !value.disabled);
    const allChecked = validData.length > 0 && validData.every(value => value.checked === true);
    const allUnChecked = validData.every(value => !value.checked);
    this.allChecked = allChecked;
    this.indeterminate = !allChecked && !allUnChecked;
  }



  async send() {
    let bills = this.salesOrder.filter((data: any) => data.checked).filter((data: any) => !data.cqt_status || data.cqt_status == 1); // lọc ra những hóa đơn đã chọn và chưa gửi lên CQT;
    const batchSize = 1;
    const totalBatches = Math.ceil(bills.length / batchSize);

    if (!bills.length) {
      this.vhComponent.alertMessageDesktop('error', this.lang.translate("Please select at least one invoice"));
      return;
    }

    // Nếu có bật cqt_vat_ecommerce_use_latest_order_update_time thì sắp xếp theo thời gian cập nhật trạng thái gần nhất của đơn hàng từ sàn, 
    // cũ gửi trước, mới gửi sau
    if(this.cqt_vat_ecommerce_use_latest_order_update_time) {
      bills = bills.sort((a, b) => a.update_time - b.update_time);
    }

    await this.vhComponent.showLoadingNotDuration(this.lang.translate("Sending..."));

    try {
      for (let i = 0; i < bills.length; i += batchSize) {
        const batch = bills.slice(i, i + batchSize);
        const currentBatchIndex = Math.floor(i / batchSize) + 1;

        const promises = batch.map(bill => {
          console.log(`Processing bill: ${bill.code}`);
          return this.sendCQT(bill);
        });

        try {
          const results = await this.vhQuery.asyncPromiseAll(promises);
          console.log(`Batch ${currentBatchIndex}/${totalBatches} done`, results);
        } catch (batchError) {
          console.error(`Error in batch ${currentBatchIndex}:`, batchError);
        }
      }
    } finally {
      // ✅ luôn được gọi sau khi xử lý xong mọi batch
      await this.vhComponent.hideLoading();
    }
  }


  /**
   * hàm này gửi hóa đơn lên cqt và ký luôn
   * @param invoice
   */
  sendCQT(invoice) {
    return new Promise((resolve, reject) => {
      // trường hợp chưa có tạo kết nối hóa đơn điện tử
      if (!this.currentBranch.einvoices?.length) {
        this.vhComponent.alertMessageDesktop("error", this.lang.translate("Please connect e-invoice for this branch"));
        resolve('');
        return;
      }
      // trường hợp chỉ có 1 kết nối hóa đơn điện tử thì tạo thẳng luôn
      if (this.currentBranch.einvoices.length && this.currentBranch.einvoices.length == 1) {
        // trường hợp ncc hóa đơn điện tử là M-invoice
        if (this.currentBranch.einvoices[0].invoice_provider == 'M-invoice') {

          if (!this.currentBranch.einvoices[0].invoiceSeries.length) {
            this.vhComponent.alertMessageDesktop("error", this.lang.translate("Please connect e-invoice for this branch"));
            resolve('');
            return;
          }
        }

        this.changeBill_byLocal_toInvoice_byMinvoice(invoice, this.currentBranch.einvoices[0].invoiceSeries[0])
          .then((rsp) => {
            if (rsp.vcode == 0) {
              // Ký tự động ngay khi gửi hóa đơn lên Cơ quan thuế
              if (this.cqt_invoice_auto_sign) {
                this.vhEinvoice.saveInvoice_Sign_byMinvoice(this.currentBranch.taxcode, this.currentBranch.einvoices[0].token, 1, rsp.data)
                  .then(async (res: any) => {
                    console.log('res', res);
                    if (res.code == '00') {
                      invoice.cqt_status = 3;
                      invoice.status = this.lang.translate("Sent to CQT");
                      this.vhQuerySales.updateSalesOrderTiki_byFile([invoice.code.toString()], { cqt_status: 3 }, this.infoShop.access_token, this.infoShop._id)
                        .then((array: any) => {
                          console.log('updateSalesOrderTiki_byFile', array);
                          resolve(true);
                        }, error => {
                          console.log(error);
                        });
                      resolve('');
                    }
                    else if (res.code == '9999') {
                      reject(invoice._id);
                      this.vhComponent.alertMessageDesktop("error", invoice.bill_code + ' ' + this.lang.translate("Vui lòng kiểm tra lại thông tin hóa đơn đẩy lên CQT chưa phù hợp"));
                    }
                    else if (res.code == '296') {
                      resolve(true);
                      this.vhComponent.alertMessageDesktop("error", this.lang.translate("Không thể gửi những hóa đơn có ngày lập hóa đơn nhỏ hơn ngày lập hóa đơn cuối cùng đã gửi lên Cơ quan thuế. <br> Nếu cần gửi những hóa đơn cũ hơn, bạn cần tắt cấu hình 'Sử dụng thời gian cập nhật trạng thái gần nhất của đơn hàng từ sàn để thiết lập ngày lập hóa đơn' trong Cài đặt"), 4000);
                    }
                    else {
                      reject(invoice._id);
                      this.vhComponent.alertMessageDesktop("error", invoice.bill_code + ' ' + this.lang.translate(res.message));
                    }
                  }).catch((error: any) => {
                    console.error('Error saving invoice:', error);
                    reject(invoice._id);
                    this.vhComponent.alertMessageDesktop("error", invoice.bill_code + ' ' + this.lang.translate("An error occurred while sending the invoice to CQT. Please try again later."));
                  });
              }
              else {
                this.vhEinvoice.saveInvoice_noSign_byMinvoice(this.currentBranch.taxcode, this.currentBranch.einvoices[0].token, 1, rsp.data)
                  .then(async (res: any) => {
                    console.log('res', res);
                    if (res.code == '00') {
                      invoice.cqt_status = 2;
                      invoice.status = this.lang.translate("Sent to CQT");
                      this.vhQuerySales.updateSalesOrderTiki_byFile([invoice.code.toString()], { cqt_status: 2 }, this.infoShop.access_token, this.infoShop._id)
                        .then((array: any) => {
                          console.log('updateSalesOrderTiki_byFile', array);
                          resolve(true);
                        }, error => {
                          console.log(error);
                        });
                      resolve('');
                    }
                    else if (res.code == '9999') {
                      reject(invoice._id);
                      this.vhComponent.alertMessageDesktop("error", invoice.bill_code + ' ' + this.lang.translate("Vui lòng kiểm tra lại thông tin hóa đơn đẩy lên CQT chưa phù hợp"));
                    }
                    else if (res.code == '296') {
                      resolve(true);
                      this.vhComponent.alertMessageDesktop("error", this.lang.translate("Không thể gửi những hóa đơn có ngày lập hóa đơn nhỏ hơn ngày lập hóa đơn cuối cùng đã gửi lên Cơ quan thuế. <br> Nếu cần gửi những hóa đơn cũ hơn, bạn cần tắt cấu hình 'Sử dụng thời gian cập nhật trạng thái gần nhất của đơn hàng từ sàn để thiết lập ngày lập hóa đơn' trong Cài đặt"), 4000);
                    }
                    else {
                      reject(invoice._id);
                      this.vhComponent.alertMessageDesktop("error", invoice.bill_code + ' ' + this.lang.translate(res.message));
                    }
                  }).catch((error: any) => {
                    console.error('Error saving invoice:', error);
                    reject(invoice._id);
                    this.vhComponent.alertMessageDesktop("error", invoice.bill_code + ' ' + this.lang.translate("An error occurred while sending the invoice to CQT. Please try again later."));
                  });
              }
            }
          }, (error: any) => {
            console.log('error', error);
          });
      }
    });
  }



  /** Gửi hóa đơn lên Cơ quan thuế kèm phí vận chuyển */
  cqt_submit_invoice_with_shipping_fee = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_submit_invoice_with_shipping_fee;
  /** Gửi hóa đơn lên Cơ quan thuế kèm phí giao dịch/phí sàn */
  cqt_submit_invoice_with_ecommerce_fee = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_submit_invoice_with_ecommerce_fee;
  /** Giá bán hàng hóa, dịch vụ... trên sàn đã bao gồm thuế VAT */
  cqt_vat_ecommerce_already_Included = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_already_Included;
  /** % thuế VAT */
  cqt_vat_ecommerce_rate = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_rate;
  /** Ký tự động ngay khi gửi hóa đơn lên Cơ quan thuế */
  cqt_invoice_auto_sign = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_invoice_auto_sign;
  /** Giá bán hàng hóa, dịch vụ trên sàn đã bao gồm VAT, tính theo VAT hàng hóa, dịch vụ liên kết */
  cqt_vat_ecommerce_by_linked_product = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_by_linked_product;
  /** Hàng khuyến mãi được phép xuất 0đ. Nếu là true thì tchat2 sẽ có các cột = 0, ma_thue vẫn là số bình thường */
  cqt_vat_ecommerce_allow_promotional_items_zero_price = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_allow_promotional_items_zero_price;
  /** Xuất riêng chiết khấu/giảm giá của sản phẩm thành 1 dòng riêng khi gửi CQT */
  cqt_vat_ecommerce_separate_discount_line = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_separate_discount_line;
  /** Vẫn giữ hàng xuất bán cho sản phẩm tặng/khuyến mãi khi gửi CQT */
  cqt_vat_ecommerce_keep_promotional_items_as_sold = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_keep_promotional_items_as_sold;
  /** Sử dụng thời gian cập nhật trạng thái gần nhất của đơn hàng từ sàn để thiết lập ngày lập hóa đơn */
  cqt_vat_ecommerce_use_latest_order_update_time = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_use_latest_order_update_time;
  /** Xuất hóa đơn từ sàn với thông tin người mua kèm theo tên sàn và mã đơn hàng */
  cqt_vat_ecommerce_buyer_info_with_platform_and_so_benh_an = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_buyer_info_with_platform_and_so_benh_an;
  /** Xuất hóa đơn từ sàn với thông tin thanh toán mặc định là 'Chuyển khoản' */
  cqt_vat_ecommerce_default_payment_method_chuyen_khoan: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_default_payment_method_chuyen_khoan;
  /** Mã sản phẩm khi gửi CQT là mã vạch sản phẩm */
  cqt_vat_ecommerce_item_code_is_barcode = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_item_code_is_barcode;
  /** Giá hàng hóa, dịch vụ... gửi CQT là giá bán đã giảm */
  cqt_vat_ecommerce_item_price_is_discounted = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').cqt_vat_ecommerce_item_price_is_discounted;



  /**
   * @example
   * this.vhQuerySales.changeBill_byLocal_toInvoice_byMinvoice(id_bill, invoiceSeries)
      .then((rsp) => {
        //-----------your code here-----------
      }, (error: any) => {
          console.log('error', error);
      });
   * @param id_bill
   * @param invoiceSeries
   * @returns Promise => {vcode, msg, data(array)}
   *
   *
   * Cấu trúc cần xử lý 3 cái:
   *  - Danh sách hóa đơn chi tiết:
   *    + Check trường hợp combo
   *  - Phí vận chuyển
   *  - Chiết khấu thương mại
   *
   *
   * Ghi chú:
   *  - Trường hợp có bật cqt_vat_ecommerce_already_Included, thì phải tính lại Giá để total sau khi áp thuế (thuế từ phần cài đặt của app mình) và Chiết khấu là không thay đổi.
   *  - tchat2 và tchat3 thì discountAmount và discountPercentage sẽ = 0
   *  - Chiết khấu thương mại thì Thuế = 0
   *  - Combo sẽ không có trường hợp tất cả các sản phẩm bên trong có giá = 0 (đã bắt khi liên kết sản phẩm) nên có thể bỏ qua trường hợp này
   *  - Đối với combo liên kết, Trường hợp cqt_vat_ecommerce_by_linked_product thì bắt ở combo nhưng vẫn giữ code ở invoice_detail cha để logic không bị thay đổi. Trường hợp cqt_vat_ecommerce_already_Included thì bắt ở invoice_detail cha, không cần bắt ở combo nữa, vì khi chia ra tỷ lệ vẫn sẽ đảm bảo do đã tính ở cha
   */
  public changeBill_byLocal_toInvoice_byMinvoice(bill: any, invoiceSeries: string): Promise<any> {
    return new Promise<any>((resolve, reject) => {
      (async () => {
        try {
          
          /** Chứa thông tin hóa đơn và bill details sẽ gửi lên cqt */
          let invoices = new Array();
          /** Danh sách bill details */
          var invoice_details = new Array();

          console.log('bill', bill);


          /* ---------------------------------------------------------- Khởi tạo invoice ---------------------------------------------------------- */

          const invoice: any = {
            inv_invoiceSeries: invoiceSeries,
            inv_invoiceIssuedDate: this.vhAlgorithm.formatDateToString(new Date()),
            inv_currencyCode: "VND",
            inv_exchangeRate: 1,

            inv_discountAmount: 0, // Tổng tiền chiết khấu (tổng của cột Tiền chiết khấu)
            inv_TotalAmountWithoutVat: 0, // Cộng tiền hàng (Đã trừ CK) Chưa bao gồm VAT (cột Thành tiền trước thuế, = +tchat1 -tchat3 bỏ qua tchat2)
            inv_vatAmount: 0, // Tiền thuế GTGT (= Cộng tiền hàng (Đã trừ CK) Chưa bao gồm VAT * bill.tax / 100)
            inv_TotalAmount: 0, // Tổng cộng tiền thanh toán (sau thuế) (= Cộng tiền hàng (Đã trừ CK) Chưa bao gồm VAT + Tiền thuế GTGT) tương đương trườnG payment của mình

            key_api: bill.code,
            so_benh_an: bill.code
          };



          /* --------- Xử lý cqt_vat_ecommerce_use_latest_order_update_time thì lấy ngày cập nhật trạng thái gần nhất của đơn hàng từ sàn --------- */
          if (this.cqt_vat_ecommerce_use_latest_order_update_time) {
            invoice.inv_invoiceIssuedDate = this.vhAlgorithm.formatDateToString(new Date(bill.updated_at));
          }
    


          /* --------------------------------------------------------- Thông tin người mua -------------------------------------------------------- */

          /** Tên người mua mặc định nếu không có thông tin trong hóa đơn */
          let buyerDisplayNameDefault = 'Người mua không lấy hóa đơn';
          /* ------ Xử lý cqt_vat_ecommerce_buyer_info_with_platform_and_so_benh_an --------------------------------------------------------------- */
          // Nếu bật thì sẽ thành: Tên sàn - Người mua không lấy hóa đơn - Mã đơn hàng
          if(this.cqt_vat_ecommerce_buyer_info_with_platform_and_so_benh_an) {
            buyerDisplayNameDefault = `Tiki - ${buyerDisplayNameDefault} - ${invoice.so_benh_an}`;
          }
        
          /* ------ Xử lý cqt_vat_ecommerce_default_payment_method_chuyen_khoan ------------------------------------------------------------------- */
          // Nếu có bật thì luôn là 'Chuyển khoản', nếu không thì sẽ lấy theo của đơn hàng
          if(this.cqt_vat_ecommerce_default_payment_method_chuyen_khoan) {
            invoice.inv_paymentMethodName = 'Chuyển khoản';
          }
          else {
            invoice.inv_paymentMethodName = bill.payment.method == 'cod' ? 'Tiền mặt' : 'Chuyển khoản';
          }

          invoice.inv_buyerDisplayName = bill.invoice?.tax_info?.company_name ? bill.invoice?.tax_info?.company_name : bill.tax_buyerDisplayName ? bill.tax_buyerDisplayName : buyerDisplayNameDefault;
          invoice.inv_buyerLegalName = bill.invoice?.tax_info?.company_name ? bill.invoice?.tax_info?.company_name : bill.tax_buyerLegalName ? bill.tax_buyerLegalName : '';
          invoice.inv_buyerTaxCode = bill.invoice?.tax_info?.code ? bill.invoice?.tax_info?.code : bill.tax_buyerTaxCode ? bill.tax_buyerTaxCode : '';
          invoice.inv_buyerAddressLine = bill.invoice?.tax_info?.address ? bill.invoice?.tax_info?.address : bill.tax_buyerAddressLine ? bill.tax_buyerAddressLine : '';
          invoice.inv_buyerEmail = bill.invoice?.tax_info?.email ? bill.invoice?.tax_info?.email : bill.tax_buyerEmail ? bill.tax_buyerEmail : '';


          /* ------------------------------------------------------ Tính toán invoice details ----------------------------------------------------- */

          /** Thuế sàn */
          let platformTax = 0;
          /** Thuế của hóa đơn (lấy từ sàn / từ cài đặt) */
          let billTax = 0;
          // nếu bật cqt_vat_ecommerce_already_Included thì lấy thuế từ cài đặt
          if (this.cqt_vat_ecommerce_already_Included) {
            billTax = this.cqt_vat_ecommerce_rate || 0;
          }
          else {
            billTax = platformTax;
          }

          /** Để cộng dồn tiền seller giảm giá phí ship trong từng bill detail shipping_fee_discountAmount để trừ vào phí sản trả Tiki vì total_seller_fee là bao gồm giảm giá phí vận chuyển */
          let shipping_fee_discountAmount = 0;

          // Lăp qua bill details
          for (let i = 0; i < bill['items'].length; i++) {
            let product: any = bill['items'][i];
            /** Sản phẩm liên kết với sản phẩm của sàn */
            const productLinked = this.findProductLinked(product);
            // Khởi tạo chi tiết hóa đơn
            let invoice_detail = this.billService.createEInvoiceDetailTemplate();

            /** Đơn giá */
            let unitPrice = product.price;
            /** Số lượng */
            const quantity = product.qty;
            /** Giảm giá của seller (không tính sàn) */
            let discountAmount = product.seller_income_detail?.discount?.discount_coupon?.seller_discount || 0;
            /** Total chưa có thuế */
            let totalAmountWithoutVat = (unitPrice * quantity) - discountAmount;
            /** Tính chất hàng hóa. Nếu inv_TotalAmountWithoutVat = 0 thì tchat = 2, nếu không thì tchat = 1 */
            const tchat = totalAmountWithoutVat === 0 ? 2 : 1;
            /** Thuế của bill detail này. Nếu có sản phẩm liên kết và bật setting cqt_vat_ecommerce_by_linked_product thì sẽ lấy thuế từ sản phẩm liên kết, ngược lại thì vẫn lấy từ billTâx như cũ  */
            const invoiceDetailTax = (this.cqt_vat_ecommerce_by_linked_product && productLinked && productLinked.tax !== undefined) ? productLinked.tax : billTax;

            // Gán các giá trị chung
            invoice_detail.tchat = tchat; // Tính chất hàng hóa: Hàng hóa dịch vụ (giá trị tchat là 1); Khuyến mại (giá trị tchat là 2); Chiết khấu thương mại (giá trị tchat là 3); Ghi chú/ diễn giải (giá trị tchat là 4); Hàng hóa đăc trưng (giá trị tchat là 5)
            invoice_detail.stt_rec0 = invoice_details.length + 1;
            invoice_detail.inv_itemCode = product.product.id;
            invoice_detail.inv_unitCode = 'Cái';
            invoice_detail.inv_quantity = quantity;
            invoice_detail.inv_unitPrice = unitPrice; // Đơn giá
            invoice_detail.ma_thue = invoiceDetailTax; // % thuế


            /* ------ Xử lý tchat --------------------------------------------------------------------------- */
            if (tchat === 2) {
              // Gán lại tên
              invoice_detail.inv_itemName = 'Hàng khuyến mãi/hàng tặng ' + product.product.name;
              // Tính lại giá trị trợ giá (chiết khấu) của chi tiết hóa đơn trường hợp tchat2
              invoice_detail.inv_discountAmount = 0;

              /* ------ Xử lý cqt_vat_ecommerce_already_Included cqt_vat_ecommerce_by_linked_product --------- */
              if(this.cqt_vat_ecommerce_already_Included || this.cqt_vat_ecommerce_by_linked_product) {
                // Tính lại giá
                invoice_detail.inv_unitPrice = this.billService.calculateNewPriceToUseSettingTax(invoice_detail.inv_unitPrice, invoice_detail.inv_quantity, invoice_detail.inv_discountAmount, invoice_detail.ma_thue, platformTax);
              }

              /* ------ Xử lý Hàng khuyến mãi được phép xuất 0đ ----------------------------------------------- */
              // Nếu có bật cqt_vat_ecommerce_allow_promotional_items_zero_price và là tchat2 thì cần gán lại giá = 0, để các phép tính cho những trường khác bên dưới cũng sẽ tự tính = 0
              if(this.cqt_vat_ecommerce_allow_promotional_items_zero_price) {
                invoice_detail.inv_unitPrice = 0;
              }

              /* ------ Xử lý Vẫn giữ hàng xuất bán cho sản phẩm tặng/khuyến mãi khi gửi CQT ------------------ */
              // Nếu có bật cqt_vat_ecommerce_keep_promotional_items_as_sold và là tchat2 thì sẽ set lại nso thành tchat1, discountAmount và discountPercentage vẫn là 0
              if(this.cqt_vat_ecommerce_keep_promotional_items_as_sold) {
                // Gán lại tchat
                invoice_detail.tchat = 1; // Gán lại tchat thành 1
                // Gán lại tên
                invoice_detail.inv_itemName = product.product.name;
              }

              // Trợ giá (chiết khấu) tính theo %
              invoice_detail.inv_discountPercentage = 0;
            }
            else {
              // Gán lại tên
              invoice_detail.inv_itemName = product.product.name;
              // Trợ giá (chiết khấu) của nhà bán hàng trên mỗi chi tiết hóa đơn = seller_discount + shopee_discount
              invoice_detail.inv_discountAmount = discountAmount;

              /* ------ Xử lý Giá hàng hóa, dịch vụ... gửi CQT là giá bán đã giảm ----------------------------------------- */
              if(this.cqt_vat_ecommerce_item_price_is_discounted) {
                // Gán lại thành giá đã giảm
                invoice_detail.inv_unitPrice = invoice_detail.inv_unitPrice - invoice_detail.inv_discountAmount / invoice_detail.inv_quantity;
                // Gán lại Chiết khấu = 0
                invoice_detail.inv_discountAmount = 0;
              }

              /* ------ Xử lý cqt_vat_ecommerce_already_Included cqt_vat_ecommerce_by_linked_product --------- */
              if(this.cqt_vat_ecommerce_already_Included || this.cqt_vat_ecommerce_by_linked_product) {
                // Tính lại giá
                invoice_detail.inv_unitPrice = this.billService.calculateNewPriceToUseSettingTax(invoice_detail.inv_unitPrice, invoice_detail.inv_quantity, invoice_detail.inv_discountAmount, invoice_detail.ma_thue, platformTax);
              }

              // Trợ giá (chiết khấu) tính theo %
              invoice_detail.inv_discountPercentage = invoice_detail.inv_discountAmount / (invoice_detail.inv_unitPrice * invoice_detail.inv_quantity) * 100;
            }

            // Giá cuối (đã trừ trợ giá) của chi tiết hóa đơn
            invoice_detail.inv_TotalAmountWithoutVat = invoice_detail.inv_unitPrice * invoice_detail.inv_quantity - invoice_detail.inv_discountAmount;
            //Thuế VAT được tính theo chi tiết hóa đơn
            invoice_detail.inv_vatAmount = invoice_detail.inv_TotalAmountWithoutVat * invoice_detail.ma_thue / 100;
            //Tổng giá trị hàng hóa kê khai theo chi tiết hóa đơn
            invoice_detail.inv_TotalAmount = invoice_detail.inv_TotalAmountWithoutVat + invoice_detail.inv_vatAmount;

            // Nếu như có liên kết
            if (productLinked) {
              // Nểu liên kết với sản phẩm
              if (productLinked.type === VhType.PRODUCT) {
                // Thay đổi inv_itemName và inv_itemCode
                invoice_detail.inv_itemName = productLinked.name;

                /* ------ Xử lý cqt_vat_ecommerce_item_code_is_barcode ------------------------------------------ */
                if(this.cqt_vat_ecommerce_item_code_is_barcode) {
                  const barcode = this.vhQuerySales.getUnit_byRatio(productLinked.units, 1)?.barcode;
                  // Nếu có barcode thì dùng barcode
                  if(barcode) {
                    invoice_detail.inv_itemCode = barcode;
                  }
                  // Nếu không có barcode thì thông báo
                  else {
                    this.vhComponent.hideLoading();
                    this.vhComponent.alertMessageDesktop("error", this.lang.translate("Gửi hóa đơn lên CQT thất bại. Hóa đơn có chứa sản phẩm chưa có mã vạch, vui lòng bổ sung và thử lại. Hoặc bỏ chọn cấu hình Mã sản phẩm khi xuất hóa đơn là mã vạch để tiếp tục"), 5000);
                    return;
                  }
                }
                else {
                  invoice_detail.inv_itemCode = productLinked.id_subproduct ? productLinked.id_subproduct : productLinked['_id'];
                }
                
                invoice_details = this.billService.addEInvoiceDetail(invoice_detail, invoice_details, {cqt_vat_ecommerce_separate_discount_line: this.cqt_vat_ecommerce_separate_discount_line});
              }
              // Nếu liên kết với combo
              else if (productLinked.type === VhType.COMBO) {
                const priceCombo = productLinked.units[0].price;
                if (priceCombo == 0) {
                  const errorZeroPriceComboMessage = this.lang.translate("Unable to issue the invoice because a linked combo product has a price of 0", { invoice: bill.code });
                  this.vhComponent.hideLoading();
                  this.vhComponent.alertMessageDesktop("error", errorZeroPriceComboMessage);
                  return;
                }
                
                // loop qua combo rồi tính lại chi tiết hóa đơn
                for (let j = 0; j < productLinked.combos.length; j++) {
                  const combo = productLinked.combos[j];
                  // Khởi tạo chi tiết hóa đơn
                  let combo_detail = this.billService.createEInvoiceDetailTemplate();
                  
                  /** Tỉ lệ phần trăm của giá sản phẩm và tổng giá combo */
                  const percentRate = (combo.price * combo.quantity) / priceCombo;
                  /** Tính chất hàng hóa. Nếu inv_TotalAmountWithoutVat = 0 thì tchat = 2, nếu không thì tchat = 1 */
                  const tchatCombo = (!percentRate || combo.price == 0 || invoice_detail.inv_TotalAmountWithoutVat * percentRate == 0) ? 2 : 1;
                  /** Sản phẩm trong combo, get lại để lấy tax */
                  let detailproduct = this.vhQuerySales.getlocalDetailProduct(combo.id_subproduct ? combo.id_subproduct : combo.id_product);
                  /** Thuế của bill detail này. Nếu có sản phẩm liên kết và bật setting cqt_vat_ecommerce_by_linked_product thì sẽ lấy thuế từ sản phẩm liên kết, ngược lại thì vẫn lấy từ billTâx như cũ  */
                  const invoiceDetailComboTax = (this.cqt_vat_ecommerce_by_linked_product && detailproduct && detailproduct.tax !== undefined) ? detailproduct.tax : invoice_detail.ma_thue;
                  /** Đơn giá */
                  const unitPrice = ((invoice_detail.inv_TotalAmountWithoutVat + invoice_detail.inv_discountAmount) * percentRate) / (combo.quantity * invoice_detail.inv_quantity);
  
                  // Gán các giá trị chung
                  combo_detail.tchat = tchatCombo;
                  combo_detail.stt_rec0 = invoice_details.length + 1;
                  combo_detail.inv_unitCode = 'Cái';
                  combo_detail.inv_quantity = combo.quantity * invoice_detail.inv_quantity;
                  combo_detail.inv_unitPrice = unitPrice; // Giá gốc bán sản phẩm trên sàn shopee
                  combo_detail.ma_thue = invoiceDetailComboTax;


                  /* ------ Xử lý cqt_vat_ecommerce_item_code_is_barcode ------------------------------------------ */
                  if(this.cqt_vat_ecommerce_item_code_is_barcode) {
                    const barcode = this.vhQuerySales.getUnit_byRatio(combo.units, combo.ratio)?.barcode;
                    // Nếu có barcode thì dùng barcode
                    if(barcode) {
                      combo_detail.inv_itemCode = barcode;
                    }
                    // Nếu không có barcode thì thông báo
                    else {
                      this.vhComponent.hideLoading();
                      this.vhComponent.alertMessageDesktop("error", this.lang.translate("Gửi hóa đơn lên CQT thất bại. Hóa đơn có chứa sản phẩm chưa có mã vạch, vui lòng bổ sung và thử lại. Hoặc bỏ chọn cấu hình Mã sản phẩm khi xuất hóa đơn là mã vạch để tiếp tục"), 5000);
                      return;
                    }
                  }
                  else {
                    combo_detail.inv_itemCode = combo.id_subproduct ? combo.id_subproduct : combo.id_product;
                  }
  
  
                  /* ------ Xử lý tchat --------------------------------------------------------------------------- */
                  if(tchatCombo === 2) {
                    // Gán lại tên
                    combo_detail.inv_itemName = 'Hàng khuyến mãi/hàng tặng ' + combo.name;
                    // Tính lại giá trị trợ giá (chiết khấu) của chi tiết hóa đơn trường hợp tchat2
                    combo_detail.inv_discountAmount = 0;
  
                    /* ------ Xử lý cqt_vat_ecommerce_by_linked_product --------- */
                    if(this.cqt_vat_ecommerce_by_linked_product) {
                      // Tính lại giá
                      combo_detail.inv_unitPrice = this.billService.calculateNewPriceToUseSettingTax(combo_detail.inv_unitPrice, combo_detail.inv_quantity, combo_detail.inv_discountAmount, combo_detail.ma_thue, platformTax);
                    }
  
                    /* ------ Xử lý Hàng khuyến mãi được phép xuất 0đ ----------------------------------------------- */
                    // Nếu có bật cqt_vat_ecommerce_allow_promotional_items_zero_price và là tchat2 thì cần gán lại giá = 0, để các phép tính cho những trường khác bên dưới cũng sẽ tự tính = 0
                    if(this.cqt_vat_ecommerce_allow_promotional_items_zero_price) {
                      combo_detail.inv_unitPrice = 0;
                    }

                    /* ------ Xử lý Vẫn giữ hàng xuất bán cho sản phẩm tặng/khuyến mãi khi gửi CQT ------------------ */
                    // Nếu có bật cqt_vat_ecommerce_keep_promotional_items_as_sold và là tchat2 thì sẽ set lại nso thành tchat1, discountAmount và discountPercentage vẫn là 0
                    if(this.cqt_vat_ecommerce_keep_promotional_items_as_sold) {
                      // Gán lại tchat
                      combo_detail.tchat = 1; // Gán lại tchat thành 1
                      // Gán lại tên
                      combo_detail.inv_itemName = combo.name;
                    }
  
                    // Trợ giá (chiết khấu) tính theo %
                    combo_detail.inv_discountPercentage = 0;
                  }
                  else {
                    // Gán lại tên
                    combo_detail.inv_itemName = combo.name;
                    // Trợ giá (chiết khấu) của nhà bán hàng trên mỗi chi tiết hóa đơn = seller_discount + shopee_discount
                    combo_detail.inv_discountAmount = this.billService.roundTo4Decimal(invoice_detail.inv_discountAmount * percentRate);

                    /* ------ Xử lý Giá hàng hóa, dịch vụ... gửi CQT là giá bán đã giảm ----------------------------------------- */
                    if(this.cqt_vat_ecommerce_item_price_is_discounted) {
                      // Gán lại thành giá đã giảm
                      combo_detail.inv_unitPrice = combo_detail.inv_unitPrice - combo_detail.inv_discountAmount / combo_detail.inv_quantity;
                      // Gán lại Chiết khấu = 0
                      combo_detail.inv_discountAmount = 0;
                    }
  
                    /* ------ Xử lý cqt_vat_ecommerce_by_linked_product --------- */
                    if(this.cqt_vat_ecommerce_by_linked_product) {
                      // Tính lại giá
                      combo_detail.inv_unitPrice = this.billService.calculateNewPriceToUseSettingTax(combo_detail.inv_unitPrice, combo_detail.inv_quantity, combo_detail.inv_discountAmount, combo_detail.ma_thue, platformTax);
                    }
  
                    // Trợ giá (chiết khấu) tính theo %
                    combo_detail.inv_discountPercentage = combo_detail.inv_discountAmount / (combo_detail.inv_unitPrice * combo_detail.inv_quantity) * 100;
                  }
  
                  // Giá cuối (đã trừ trợ giá) của chi tiết hóa đơn
                  combo_detail.inv_TotalAmountWithoutVat = combo_detail.inv_unitPrice * combo_detail.inv_quantity - combo_detail.inv_discountAmount;
                  // Thuế VAT được tính theo chi tiết hóa đơn
                  combo_detail.inv_vatAmount = combo_detail.inv_TotalAmountWithoutVat * combo_detail.ma_thue / 100;
                  // Tổng giá trị hàng hóa kê khai theo chi tiết hóa đơn
                  combo_detail.inv_TotalAmount = combo_detail.inv_TotalAmountWithoutVat + combo_detail.inv_vatAmount;
  
                  // Thêm vào mảng
                  invoice_details = this.billService.addEInvoiceDetail(combo_detail, invoice_details, {cqt_vat_ecommerce_separate_discount_line: this.cqt_vat_ecommerce_separate_discount_line});
                }
              }
            }
            else {
              invoice_details = this.billService.addEInvoiceDetail(invoice_detail, invoice_details, {cqt_vat_ecommerce_separate_discount_line: this.cqt_vat_ecommerce_separate_discount_line});
            }

            if(tchat === 1) {
              // Cộng dồn tiền seller giảm giá phí ship
              shipping_fee_discountAmount += product.seller_income_detail.discount?.discount_shipping_fee?.seller_subsidy || 0;
            }
          }



          /* -------------------------------------------------------- Xử lý Phí vận chuyển -------------------------------------------------------- */

          // Xử lý cho phí
          if (this.cqt_submit_invoice_with_shipping_fee && bill.invoice.shipping_amount_after_discount + shipping_fee_discountAmount > 0) {
            // Khởi tạo chi tiết hóa đơn
            let invoice_detail = this.billService.createEInvoiceDetailTemplate();

            /** Giảm giá (chỉ lấy giảm giá của Tiki) */
            const discountAmount = bill.invoice.shipping_discount_amount - shipping_fee_discountAmount;
            /** Đơn giá */
            let unitPrice = bill.invoice.shipping_amount_after_discount + discountAmount + shipping_fee_discountAmount;
            /** Số lượng */
            const quantity = 1;
            /** Total chưa có thuế */
            let totalAmountWithoutVat = (unitPrice * quantity) - discountAmount;

            // Gán các giá trị chung
            invoice_detail.tchat = 1;
            invoice_detail.stt_rec0 = invoice_details.length + 1;
            invoice_detail.inv_itemCode = 'phi_van_chuyen';
            invoice_detail.inv_itemName = 'Phí vận chuyển';
            invoice_detail.inv_unitCode = 'Lần';
            invoice_detail.inv_quantity = quantity;
            invoice_detail.inv_unitPrice = unitPrice;
            invoice_detail.inv_discountAmount = discountAmount; // giảm giá (Tiền chiết khấu)
            invoice_detail.inv_TotalAmountWithoutVat = totalAmountWithoutVat; // Thành tiền trước thuế
            invoice_detail.ma_thue = billTax; // % thuế

            /* ------ Xử lý cqt_vat_ecommerce_already_Included ---------------------------------------------- */
            if (this.cqt_vat_ecommerce_already_Included) {
              // Tính lại giá
              invoice_detail.inv_unitPrice = this.billService.calculateNewPriceToUseSettingTax(invoice_detail.inv_unitPrice, invoice_detail.inv_quantity, invoice_detail.inv_discountAmount, invoice_detail.ma_thue, platformTax);
            }

            // Trợ giá (chiết khấu) tính theo %
            invoice_detail.inv_discountPercentage = invoice_detail.inv_discountAmount / (invoice_detail.inv_unitPrice * invoice_detail.inv_quantity) * 100;
            // Giá cuối (đã trừ trợ giá) của chi tiết hóa đơn
            invoice_detail.inv_TotalAmountWithoutVat = invoice_detail.inv_unitPrice - invoice_detail.inv_discountAmount;
            // Thuế VAT được tính theo chi tiết hóa đơn
            invoice_detail.inv_vatAmount = invoice_detail.inv_TotalAmountWithoutVat * invoice_detail.ma_thue / 100;
            // Tổng giá trị hàng hóa kê khai theo chi tiết hóa đơn
            invoice_detail.inv_TotalAmount = invoice_detail.inv_TotalAmountWithoutVat + invoice_detail.inv_vatAmount;

            // Thêm vào mảng
            invoice_details = this.billService.addEInvoiceDetail(invoice_detail, invoice_details, {cqt_vat_ecommerce_separate_discount_line: this.cqt_vat_ecommerce_separate_discount_line});
          }



          /* --------------------------------------------------------- Xử lý phí trả Tiki --------------------------------------------------------- */

          if (this.cqt_submit_invoice_with_ecommerce_fee) {
            /** Phí trả Tiki */
            const total_seller_fee = Math.abs(bill.invoice?.total_seller_fee) - shipping_fee_discountAmount || 0; // total_seller_fee là số âm (treated as statement entry in seller account: -25000 means expense, charge or cost)
            if (total_seller_fee > 0) {
              // Khởi tạo chi tiết hóa đơn
              let invoice_detail = this.billService.createEInvoiceDetailTemplate();

              /** Giảm giá */
              const discountAmount = 0;
              /** Đơn giá */
              let unitPrice = total_seller_fee;
              /** Số lượng */
              const quantity = 1;
              /** Total chưa có thuế */
              let totalAmountWithoutVat = (unitPrice * quantity) - discountAmount;

              // Gán các giá trị chung
              invoice_detail.tchat = 3;
              invoice_detail.stt_rec0 = invoice_details.length + 1,
              invoice_detail.inv_itemCode = 'chiet_khau_thuong_mai',
              invoice_detail.inv_itemName = 'Chiết khấu thương mại',
              invoice_detail.inv_unitCode = 'Phần',
              invoice_detail.inv_quantity = quantity,
              invoice_detail.inv_unitPrice = unitPrice,
              invoice_detail.inv_TotalAmountWithoutVat = totalAmountWithoutVat, // Thành tiền trước thuế
              invoice_detail.ma_thue = 0, // Không có thuế cho chiết khấu thương mại
              invoice_detail.inv_TotalAmount = total_seller_fee + 0 // tổng tiền sau thuế (Thành tiền sau thuế)

              // Giá cuối (đã trừ trợ giá) của chi tiết hóa đơn
              invoice_detail.inv_TotalAmountWithoutVat = invoice_detail.inv_unitPrice - invoice_detail.inv_discountAmount;
              // Thuế VAT được tính theo chi tiết hóa đơn
              invoice_detail.inv_vatAmount = invoice_detail.inv_TotalAmountWithoutVat * invoice_detail.ma_thue / 100;
              // Tổng giá trị hàng hóa kê khai theo chi tiết hóa đơn
              invoice_detail.inv_TotalAmount = invoice_detail.inv_TotalAmountWithoutVat + invoice_detail.inv_vatAmount;

              // Thêm vào mảng
              invoice_details = this.billService.addEInvoiceDetail(invoice_detail, invoice_details, {cqt_vat_ecommerce_separate_discount_line: this.cqt_vat_ecommerce_separate_discount_line});
            }
          }


          /* ---------------------------------------------- Gán lại các trường tính toán cho invoice ---------------------------------------------- */

          for (let i = 0; i < invoice_details.length; i++) {
            if (invoice_details[i].tchat == 1) {
              // tchat 1 thì cộng dồn
              invoice.inv_discountAmount += invoice_details[i].inv_discountAmount;
              invoice.inv_vatAmount += invoice_details[i].inv_vatAmount;
              invoice.inv_TotalAmountWithoutVat += invoice_details[i].inv_TotalAmountWithoutVat;
              invoice.inv_TotalAmount += invoice_details[i].inv_TotalAmount;
            } else if (invoice_details[i].tchat == 2) {
              // tchat 2 thì bỏ qua
            } else if (invoice_details[i].tchat == 3) {
              // tchat 3 thì phải trừ lại
              invoice.inv_discountAmount -= invoice_details[i].inv_discountAmount;
              invoice.inv_vatAmount -= invoice_details[i].inv_vatAmount;
              invoice.inv_TotalAmountWithoutVat -= invoice_details[i].inv_TotalAmountWithoutVat;
              invoice.inv_TotalAmount -= invoice_details[i].inv_TotalAmount;
            }
          }


          /* ----------------------------------------------------------- Trả về giá trị ----------------------------------------------------------- */

          invoices = [{ ...invoice, details: [{ data: invoice_details }] }];
          console.log('data', invoices);
          resolve({ vcode: 0, msg: 'success', data: invoices });
        } catch (err) {
          console.error('❌ Lỗi saveInvoice_noSign_byMinvoice:', err);
          reject(err);
        }
      })();
    });
  }



  /**
   * Đếm số lượng bill đang check
   */
  get countSelectedBill(): number {
    return this.salesOrder?.filter((e: any) => e.checked)?.filter((data: any) => !data.cqt_status || data.cqt_status == 1)?.length;
  }



  /**
   * Lấy ngày cập nhật của đơn hàng theo định dạng 2025-06-25
   */
  getUpdateTimeFormat(order) {
    return this.vhAlgorithm.formatDateToString(new Date(order.updated_at));
  }

}
